<template>
  <section class="counterparty">
    <!-- title -->
    <h1 class="counterparty__title page__title">Контрагент</h1>
    <!-- content and fields -->
    <div class="counterparty-content page-wrapper" v-if="user">
      <div class="counterparty-fields">
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Контрагент'"
            :id="'counterparty'"
            v-model="counterparty"
          />
        </div>
        <div class="counterparty-fields-row fields-row _inn">
          <base-input
            :label="'ИНН'"
            :id="'counterpartyInn'"
            :message="'Данный ИНН уже существует в нашей базе данных'"
            v-model="inn"
          />
        </div>
        <div class="counterparty-fields-row fields-row _address">
          <base-input
            :label="'Адрес'"
            :id="'counterpartyAddress'"
            v-model="address"
          />
        </div>
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Город'"
            :id="'counterpartyCity'"
            v-model="city"
          />
        </div>
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Страна'"
            :id="'counterpartyCountry'"
            v-model="country"
          />
        </div>
        <div class="counterparty-fields-row fields-row _index">
          <base-input
            :label="'Индекс'"
            :short="true"
            :id="'counterpartyCountry'"
            v-model="index"
          />
        </div>
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Телефон'"
            :id="'counterpartyPhone'"
            v-model="phone"
          />
        </div>
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Электронная почта'"
            :id="'counterpartyEmail'"
            v-model="email"
          />
        </div>
        <div class="counterparty-fields-row fields-row _contact-face">
          <base-input
            :label="'Контактное лицо'"
            :id="'counterpartyContactFace'"
            v-model="contactFace"
          />
        </div>
        <div class="counterparty-fields-row fields-row">
          <base-input
            :label="'Скидка'"
            :short="true"
            :id="'counterpartyDiscount'"
            v-model="discount"
          />
        </div>
      </div>
    </div>
    <!-- buttons -->
    <div class="counterparty-buttons page-buttons _save" v-if="user">
      <div class="counterparty__send _save__button">
        <base-button :type="'filled'" @click="openModal = true">
          Сохранить
        </base-button>
      </div>
    </div>
    <!-- search -->
    <div class="counterparty-search page-wrapper">
      <div class="counterparty-search-field">
        <base-input
          :id="'counterpartySearch'"
          :placeholder="!$isMobile ? 'Поиск по контрагенту или ИНН' : 'Поиск'"
          :search="true"
          :searchIcon="true"
          :suggestionWidth="380"
          :suggestions="suggestions"
          @setSuggestion="(value) => (counterparty = value)"
          @clear="counterparty = ''"
          v-model="counterparty"
        />
      </div>
    </div>
    <!-- table -->
    <div class="page-wrapper">
      <counterparty-table :counterparties="counterparties" />
    </div>
  </section>
  <base-modal
    :open="openModal"
    :text="'Отправлено'"
    @close="openModal = false"
  />
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseModal from "@/components/common/Base.Modal.vue";
import CounterpartyTable from "@/components/counterparty/CounterpartyTable.vue";

export default {
  name: "Counterparty",
  components: {
    BaseInput,
    BaseButton,
    BaseModal,
    CounterpartyTable,
  },
  data() {
    return {
      user: "administrator",
      counterparty: "",
      inn: "",
      address: "",
      city: "",
      country: "",
      index: "",
      phone: "",
      email: "",
      contactFace: "",
      discount: "",
      openModal: false,
      counterparties: [],
      suggestions: [
        "8/21-ES",
        "7/21-ES",
        "6/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
      ],
    };
  },
  mounted() {
    this.getCounterparties();
  },
  methods: {
    getCounterparties() {
      this.$api.get(`${process.env.VUE_APP_API}/counterparty`).then((res) => {
        console.log(res.data.results);
        for (let c of res.data.results) {
          const obj = {
            name: c.name,
            inn: c.inn,
            address: `${c.address_street && c.address_street + ","} ${
              c.address_city && c.address_city + ","
            } ${c.address_country && c.address_country + ","} ${
              c.address_index && c.address_index
            }`,
            number: `+${c.phone_number}`,
            email: c.email,
            contactPerson: c.contact_person,
            discount: `${c.discount}%`,
          };
          this.counterparties.push(obj);
        }
      });
    },
  },
};
</script>

